.product_showcase{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 2fr 2fr 2fr;
    grid-column-gap: 32px;
    column-gap: 32px;
    grid-row-gap: 21px;
    row-gap: 21px;
    justify-content: center;
    align-items: center;
    margin-top: 31px;
}
.item_field{
    background-color: white;
    border-radius: 16px;
    height: 233px;
    width: 195px;
    text-align: center;
    margin-top: 35px;
}
.item_field h1{
    color: #011F3B;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    margin: 0;
    font-size: 14px;
}
.item_field h2{
    color: #92BE3D;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    margin: 0;
    font-size: 14px;
}
.item_field h3{
    color: #011F3B;
    font-family: 'Barlow', sans-serif;
    margin-top: 8px;
    margin-bottom: 8px;
    font-weight: normal;
    font-size: 14px;
}
.item_field p{
    color: #ABBBC2;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    margin: 0;
    font-size: 13px;
}
.item_image{
    border: 3px solid #FFFFFF;
    border-radius: 150px;
    height: 134px;
    width: 134px;
    position: relative;
    margin:-35px auto 15px auto;
}
